@font-face {
  font-family: "DMSans";
  src: local("/fonts/DMSans-Regular.ttf");
}

@font-face {
  font-family: "DMSans";
  src: local("/fonts/DMSans-Bold.ttf");
}

body {
  font-family: "DMSans", Arial, Helvetica, sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 17px;
  line-height: 1.59;
  background: #191919;
  color: #ffffff;
}

a {
  color: #ffffff;
  cursor: pointer;
}

.active {
  transition: all 0.5s;
  color: #118da8 !important;
  text-shadow: 0px 0px 1px #118da8;
}

.wrap {
  display: flex;
  flex-wrap: wrap;
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 70px;
}

.title {
  font-size: 64px;
  line-height: 1.1;
  letter-spacing: 2.56px;
}

.title-sm {
  font-size: 18px;
  line-height: 1.5;
  color: #118da8;
}

.button {
  cursor: pointer;
  font-size: 17px;
  line-height: 27px;
  background-color: #118da8;
  border-radius: 6px;
  padding: 12px 15px;
  text-align: center;
  transition: all 1s;
  writing-mode: sideways-rl;
  backface-visibility: hidden;
  color: #ffffff;
}

.button:hover {
  background-color: rgb(212, 175, 104);
  transform: scale(1.03);
}

.wrap-row > * {
  max-width: 50%;
}

.content__img {
  width: 650px;
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    top: -50px;
  }

  to {
    opacity: 1;
    top: 0;
  }
}

.logo {
  margin-left: -20px;
  width: 159px;
  height: 32px;
}

.decore {
  position: relative;
}

.decore__item {
  position: absolute;
}

.decore__item_home {
  top: -2.98%;
  left: -2.98%;
  width: 10.46%;
}

.decore__underline {
  position: relative;
  display: inline-block;
}

.decore__underline:before {
  content: "";
  position: absolute;
  background-image: url("../img/content/decore-underline.svg");
  background-size: 100%;
  background-repeat: no-repeat;
  background-position: center;
  width: 100%;
  height: 6.75%;
  left: 0;
  bottom: 0;
}

.arrow {
  background: #118da8;
  display: block;
  border-radius: 50%;
  width: 119px;
  height: 119px;
  background-image: url("../img/icons/other/arrow.png");
  background-repeat: no-repeat;
  background-position: center;
  margin-bottom: 28px;
}

.streaming {
  display: flex;
}

.streaming a {
  width: 32px;
  height: 32px;
}

.streaming a:not(:last-child) {
  margin-right: 17px;
}

.streaming img {
  width: 100%;
  transition: transform 0.5s;
}

.streaming img:hover {
  transform: rotateZ(360deg) scale(1.2);
}

.social__item {
  width: 28px;
  height: 28px;
}

.social__item:not(:last-child) {
  margin-right: 14px;
}

.social__item:hover svg path {
  stroke: #118da8;
}

.header__wrap {
  height: 119px;
  justify-content: space-between;
  align-items: center;
}

.header__menu-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
}

.header__menu-item:not(:last-child) {
  margin-right: 20px;
}

.header__menu-item a {
  color: #ffffff;
  font-style: normal;
  font-weight: 400;
  font-size: 17px;
  line-height: 1.59;
  -moz-user-select: none;
  -khtml-user-select: none;
  user-select: none;
}

.header_scroll {
  width: 100%;
  position: fixed;
  top: 0;
  animation-name: fadeInUp;
  animation-duration: 1s;
  z-index: 400;
  background-color: rgba(0, 0, 0, 0.7333333333);
}

.home__wrap {
  padding-top: 40px;
}

.home__wrap > * {
  max-width: 50%;
}

.home__img {
  width: 100%;
}

.home__content {
  padding: 294px 0px 0px 60px;
}

.home__title {
  font-size: 70px;
  line-height: 1.26;
  letter-spacing: 3.2px;
  margin-bottom: 74px;
}

.home__title_bold {
  font-weight: 700;
  font-size: 80px;
  line-height: 1.1;
  letter-spacing: 3.2px;
}

.home-streaming__title {
  font-size: 15px;
  line-height: 1.6;
  margin-bottom: 16px;
}

.episodes__wrap {
  flex-direction: column;
  padding-top: 93px;
}

.episodes__header {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 66px;
}

.episodes__column {
  display: flex;
  flex-direction: column;
}

.episodes__item {
  display: flex;
  justify-content: space-between;
  background: #000000;
  border-radius: 32px;
  padding: 40px;
}

.episodes__item:not(:last-child) {
  margin-bottom: 60px;
}

.episodes__item-img {
  max-width: 389px;
  object-fit: cover;
}

.episodes__item-text {
  display: flex;
  flex-direction: column;
  padding-left: 53px;
  flex-grow: 0;
  align-items: flex-start;
}

.episodes__item-tag {
  font-size: 18px;
  line-height: 27px;
  background: #191919;
  padding: 0 16px;
  border-radius: 6px;
  margin-bottom: 48px;
}

.episodes__item-number {
  font-size: 17px;
  line-height: 1.59;
  color: #118da8;
  margin-bottom: 18px;
}

.episodes__item-description {
  font-size: 22px;
  line-height: 1.5;
  color: rgba(255, 255, 255, 0.7);
  margin-bottom: 18px;
}

.about__wrap {
  padding-top: 260px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: auto auto;
  grid-template-areas: "top img" "bottom img";
  align-items: center;
}

.about-block_padding-right {
  padding-right: 60px;
}

.about__title {
  font-size: 65px;
  line-height: 1.22;
  letter-spacing: 2.88px;
  margin-bottom: 28px;
}

.about__top {
  grid-area: top;
  align-self: flex-end;
}

.about__bottom {
  grid-area: bottom;
  align-self: flex-start;
  font-size: 22px;
  line-height: 1.5;
  color: rgba(255, 255, 255, 0.7);
}

.about__bottom p:not(:last-of-type) {
  margin-bottom: 33px;
}

.about__img {
  grid-area: img;
  max-width: 100%;
}

.email-form {
  display: flex;
  width: 100%;
  padding: 60px 60px 75px;
  justify-content: space-between;
  background: #000000;
  border-radius: 32px;
}

.email-form__text {
  max-width: 430px;
}

.email-form__title {
  font-size: 72px;
  line-height: 1.1;
  letter-spacing: 2.88px;
}

.email-form__inputs {
  width: 40.154%;
}

.email-form__input {
  color: #ffffff;
  border-radius: 6px;
  background-color: #191919;
  padding: 26px 19px;
  font-size: 24px;
  line-height: 1.2;
  font-weight: 400;
  width: 100%;
  margin-bottom: 20px;
}

.subscribe__wrap {
  padding-top: 200px;
}

.subscribe__decore {
  bottom: -6.7183%;
  left: -2.923%;
  width: 11.3077%;
}

.review__wrap {
  padding-top: 200px;
  justify-content: flex-start;
}

.review__item {
  max-width: 32.63889%;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  background-color: #000000;
  border-radius: 32px;
  padding: 54px 51px 51px;
  margin-bottom: 16px;
}

.review__item:not(:nth-child(3n)) {
  margin-right: 1.04166%;
}

.review__rating {
  display: flex;
}

.review__rating > *:not(:last-child) {
  margin-right: 5px;
}

.review__item-text {
  font-size: 36px;
  line-height: 1.2;
  padding: 21px 0 19px;
}

.review-author {
  font-size: 24px;
  line-height: 1.25;
  color: rgba(255, 255, 255, 0.7);
}

.footer__wrap {
  padding-top: 200px;
  display: grid;
  grid-template-columns: 35.9138% 28.1722% 35.9138%;
  grid-template-areas: "logo menu streaming";
}

.footer__column-right {
  grid-area: streaming;
}

.footer__column-left {
  grid-area: logo;
}

.footer__logo {
  font-size: 50px;
  margin-bottom: 20px;
  line-height: 1;
  -moz-user-select: none;
  -khtml-user-select: none;
  user-select: none;
  cursor: pointer;
}

.footer-menu {
  grid-area: menu;
  padding: 0 8px;
  display: flex;
}

.footer-menu__list {
  padding: 0 8px 5px;
}

.footer-menu__item {
  margin-bottom: 10px;
}

.footer-menu__item a {
  font-size: 17px;
  line-height: 1.59;
}

.footer-menu__item a:hover {
  color: #118da8;
}